* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  text-decoration: none;
  outline-offset: 20px;
  transition: 0.2s all ease;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
*:focus {
  border-radius: 3px;
  outline: 4px solid #ca6d38;
  outline-offset: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

div {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  padding: 0;
  margin: 0;
  color: inherit;
  text-decoration: none;
}

img {
  object-fit: cover;
}

button,
input {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

body {
  font-size: 1.5rem;
  line-height: 2rem;
  color: #181818;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  padding-top: 4.375rem;
  overflow: hidden;
}

.container {
  max-width: 78.6875rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}

.section-outer {
  margin-bottom: 7.5rem;
}
.section-outer--small {
  margin-bottom: 6.25rem;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2.5rem;
  font-size: 3.4375rem;
  line-height: 3.4375rem;
  text-align: center;
  letter-spacing: 0.01em;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 300;
}
.title::before, .title::after {
  width: 60vw;
  height: 0.125rem;
  margin: 0 3.75rem;
  content: "";
  background: #dfd7d3;
}
.title::before {
  margin-left: -20vw;
}
.title::after {
  margin-right: -20vw;
}
.title__text {
  width: 100%;
  max-width: fit-content;
}

.btn {
  display: inline-block;
  max-width: 100%;
  padding: 1.5rem 2.75rem;
  cursor: pointer;
  background: none;
  border: none;
}
.btn .btn-text {
  font-size: 1.25rem;
  line-height: 1.5rem;
  text-align: center;
  font-family: "Circle", sans-serif;
  font-weight: 400;
}
.btn-no-bg {
  position: relative;
  min-width: 14.4375rem;
  color: #fff;
  border: 0.125rem solid #dbdbdb;
  border-right: none;
  transition: all 0.4s ease-in-out;
}
.btn-no-bg .btn-text {
  position: relative;
}
.btn-no-bg .btn-text::before {
  position: absolute;
  top: 50%;
  bottom: 50%;
  left: calc(100% - -18px);
  width: 4.125rem;
  height: 0.625rem;
  content: "";
  background: url("data:image/svg+xml;charset=UTF-8,%3csvg width='66' height='10' viewBox='0 0 66 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M61.2071 0.292847L65.9142 4.99995L61.2071 9.70706L59.7929 8.29285L62.0858 5.99995H0V3.99995H62.0858L59.7929 1.70706L61.2071 0.292847Z' fill='%23DFD7D3'/%3e%3c/svg%3e ") no-repeat;
  transition: all 0.4s ease-in-out;
  transform: translateY(-50%);
}
.btn-no-bg::before, .btn-no-bg::after {
  position: absolute;
  right: 0;
  width: 0.125rem;
  height: 30%;
  content: "";
  background-color: #dbdbdb;
  transition: all 0.4s ease-in-out;
}
.btn-no-bg::before {
  top: 0;
}
.btn-no-bg::after {
  bottom: 0;
}
.btn-no-bg:hover {
  border-color: #ca6d38;
}
.btn-no-bg:hover::before, .btn-no-bg:hover::after {
  background-color: #ca6d38;
}
.btn-no-bg:hover .btn-text::before {
  left: calc(100% - -30px);
}
.btn-no-bg:active {
  border-color: #dbdbdb;
}
.btn-no-bg:active::before, .btn-no-bg:active::after {
  background-color: #dbdbdb;
}
.btn-no-bg:active .btn-text::before {
  left: calc(100% - -10px);
}
.btn-bg {
  color: #181818;
  background-color: #dfd7d3;
  transition: all 0.4s ease-in-out;
}
.btn-bg:hover {
  box-shadow: 0 0 1.875rem rgba(223, 215, 211, 0.2);
}
.btn-bg:active {
  box-shadow: 0 0 0.9375rem rgba(223, 215, 211, 0.1);
}
.btn-standart {
  border: 0.125rem solid #dfd7d3;
  transition: all 0.4s ease-in-out;
}
.btn-standart .btn-text {
  color: #454545;
}
.btn-standart:hover {
  border-color: #ca6d38;
}
.btn-standart:active {
  opacity: 0.8;
}

.logo__img {
  width: 14.375rem;
  height: 2.5625rem;
  object-fit: contain;
}

.menu__list {
  display: flex;
}
.menu__list-item + .menu__list-item {
  margin-left: 2.125rem;
}
.menu__list-link {
  font-size: 1.25rem;
  line-height: 1.5625rem;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 300;
}
@media (any-hover: hover) {
  .menu__list-link {
    transition: all 0.4s ease-in-out;
  }
  .menu__list-link:hover {
    color: #ca6d38;
  }
}

.burger-btn {
  position: relative;
  display: none;
  width: 1.9375rem;
  height: 0.75rem;
  margin-left: auto;
  cursor: pointer;
  cursor: pointer;
  background: none;
  border: none;
}
.burger-btn__line {
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 50%;
  width: 100%;
  height: 0.125rem;
  background-color: #ca6d38;
  transition: all 0.4s ease-in-out;
  transform: translateY(-50%);
}
.burger-btn::before, .burger-btn::after {
  position: absolute;
  right: 0;
  height: 0.125rem;
  content: "";
  background-color: #ca6d38;
  transition: all 0.4s ease-in-out;
}
.burger-btn::before {
  top: 0;
  width: 100%;
}
.burger-btn::after {
  bottom: 0;
  width: 70%;
}
@media (any-hover: hover) {
  .burger-btn:hover::after {
    width: 100%;
  }
}

.contact-phone {
  position: relative;
  display: flex;
  align-items: center;
}
.contact-phone__img {
  position: absolute;
  top: -2px;
  width: 0.9375rem;
  height: 0.9375rem;
}
.contact-phone__text {
  padding-left: 1.5625rem;
  line-height: 1.5625rem;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 400;
}
@media (any-hover: hover) {
  .contact-phone {
    transition: all 0.4s ease-in-out;
  }
  .contact-phone:hover svg {
    fill: #ca6d38;
  }
  .contact-phone:hover {
    color: #ca6d38;
  }
}

.social-item {
  position: relative;
  display: flex;
}
.social-item__img {
  position: absolute;
  top: -30%;
  left: 0;
}
.social-item__img svg {
  width: 0.9375rem;
  height: 0.9375rem;
}
.social-item__img svg path {
  fill: #fff;
  transition: all 0.4s ease-in-out;
}
.social-item__text {
  padding-left: 1.875rem;
  font-size: 1.375rem;
  line-height: 1.5625rem;
  color: #f3f1ef;
  transition: all 0.4s ease-in-out;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 400;
}
@media (any-hover: hover) {
  .social-item__text:hover {
    color: #ca6d38;
  }
  .social-item:hover path {
    fill: #ca6d38;
  }
}

.slider-navigation {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 34.75rem;
}

.slider-button {
  display: inline;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}
.slider-button svg {
  width: 5.6875rem;
  height: 0.6875rem;
  fill: #ca6d38;
}
.slider-button path {
  transition: all 0.2s ease-in-out;
}
.slider-button:hover path {
  box-shadow: 0 0.0625rem 0.25rem rgba(69, 69, 69, 0.25);
}
.slider-button:active path {
  box-shadow: 0 0.0625rem 0.25rem rgba(69, 69, 69, 0.25);
  fill: rgba(202, 109, 56, 0.8);
}
.slider-button-prev svg {
  transform: rotate(-180deg);
}

.swiper-button-disabled {
  cursor: auto;
  opacity: 0.3;
}

.swiper-pagination-bullet {
  width: 0.625rem;
  height: 0.625rem;
  background: none;
  border: 0.0625rem solid #dfd7d3;
  opacity: 1;
  transition: all 0.85s ease-in-out;
}
.swiper-pagination-bullet + .swiper-pagination-bullet {
  margin-left: 0.625rem;
}
.swiper-pagination-bullet-active {
  background: #ca6d38;
  box-shadow: 0 0.0625rem 0.25rem rgba(69, 69, 69, 0.25);
}

.swiper-slide-next,
.swiper-slide-prev {
  cursor: pointer;
}

.tabs-triggers {
  display: flex;
  justify-content: center;
  margin-bottom: 2.8125rem;
}
.tabs-triggers__item {
  position: relative;
  font-size: 1.875rem;
  line-height: 2rem;
  color: #454545;
  white-space: nowrap;
  transition: all 0.4s ease-in-out;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.tabs-triggers__item::before {
  position: absolute;
  right: 0;
  bottom: -0.3125rem;
  left: auto;
  width: 0;
  height: 0.125rem;
  content: "";
  background-color: #ca6d38;
  transition: all 0.4s ease-in-out;
}
.tabs-triggers__item + .tabs-triggers__item {
  margin-left: 2.5rem;
}
.tabs-triggers__item--active {
  color: #ca6d38;
}
.tabs-triggers__item--active::before {
  right: auto;
  left: 0;
  width: 100%;
}
.tabs-triggers__item:hover {
  color: #ca6d38;
}
.tabs-content__item {
  display: none;
}
.tabs-content__item--active {
  display: block;
}

.filter-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 5.875rem;
}
.filter-item {
  position: relative;
  font-size: 1.875rem;
  line-height: 2rem;
  color: #454545;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.filter-item::before {
  position: absolute;
  right: 0;
  bottom: -0.3125rem;
  left: auto;
  width: 0;
  height: 0.125rem;
  content: "";
  background-color: #ca6d38;
  transition: all 0.4s ease-in-out;
}
.filter-item + .filter-item {
  margin-left: 2.5rem;
}
.filter-item--active {
  color: #ca6d38;
}
.filter-item--active::before {
  right: auto;
  left: 0;
  width: 100%;
}

.breadcrumbs__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.breadcrumbs__list-item {
  margin-top: 0.625rem;
  margin-right: 1.25rem;
  font-size: 1rem;
  line-height: 1.5rem;
  color: #dfd7d3;
  transition: all 0.4s ease-in-out;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.breadcrumbs__list-item:hover a {
  color: #ca6d38;
}
.breadcrumbs__list-item:hover a::after {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='36' height='6' viewBox='0 0 36 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M33.8295 2.34569L32.2464 0.691389L32.9688 0L35.6921 2.84569L32.9688 5.69139L32.2464 5L33.8295 3.34569H0V2.34569H33.8295Z' fill='%23ca6d38'/%3e%3c/svg%3e ");
}
.breadcrumbs__list-item:last-child {
  margin-right: 0;
}
.breadcrumbs__list-item a {
  position: relative;
  padding-right: 3.5rem;
  transition: all 0.4s ease-in-out;
}
.breadcrumbs__list-item a::after {
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 50%;
  width: 2.25rem;
  height: 0.375rem;
  content: "";
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='36' height='6' viewBox='0 0 36 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M33.8295 2.34569L32.2464 0.691389L32.9688 0L35.6921 2.84569L32.9688 5.69139L32.2464 5L33.8295 3.34569H0V2.34569H33.8295Z' fill='%23DFD7D3'/%3e%3c/svg%3e ");
  transition: all 0.4s ease-in-out;
  transform: translateY(-50%);
}

.pagination {
  margin-top: 3.75rem;
}
.pagination-list {
  display: flex;
  justify-content: center;
}
.pagination-list__item {
  width: 2.1875rem;
  height: 2.1875rem;
  padding: 0.375rem 0.75rem;
  margin: 0 0.46875rem;
  font-size: 1.5rem;
  line-height: 1.5rem;
  white-space: nowrap;
  transition: all 0.4s ease-in-out;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.pagination-list__item:hover {
  color: #ca6d38;
}
.pagination-list__item--all, .pagination-list__item--first, .pagination-list__item--last {
  width: auto;
  padding: 0;
  line-height: 2rem;
  border-bottom: 0.0625rem solid #454545;
}
.pagination-list__item--all:hover, .pagination-list__item--first:hover, .pagination-list__item--last:hover {
  border-bottom-color: #ca6d38;
}
.pagination-list__item--first, .pagination-list__item--last {
  border: none;
}
.pagination-list__item--active {
  color: #ca6d38;
  border: 0.0625rem solid #ca6d38;
}

.scroll-top {
  position: fixed;
  right: 1.875rem;
  bottom: 1.875rem;
  z-index: 999;
  width: 2.8125rem;
  height: 2.8125rem;
  cursor: pointer;
  visibility: visible;
  background-color: #454545;
  border: none;
  opacity: 1;
  transition: all 0.4s ease-in-out;
}
.scroll-top__arrow {
  position: absolute;
  top: 43%;
  left: 48%;
  width: 1.25rem;
  height: 1.25rem;
  transform: translate(-50%, -50%);
}
.scroll-top__arrow svg {
  width: 1.25rem;
  height: 1.25rem;
  fill: #fff;
  transform: rotate(-90deg);
}
.scroll-top:hover {
  box-shadow: 0 0 1.875rem rgba(69, 69, 69, 0.4);
}
.scroll-top--hide {
  visibility: hidden;
  opacity: 0;
}

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  background-color: #fff;
}
.header--active .header-navigation {
  flex-direction: column;
  align-items: center;
  height: 100vh;
  padding-bottom: 3.3125rem;
  color: #fff;
  visibility: visible;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.header--active .header-navigation::before {
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  background: url("../images/default/mobile-menu-bg.webp") no-repeat;
  background-size: cover;
}
.header--active .header-contact {
  display: block;
  margin-left: 0;
  line-height: 1.5625rem;
}
.header--active .menu {
  padding: 2.5rem 4rem 3.3125rem 4rem;
}
